@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,800&display=swap);
*{box-sizing:border-box}html,body{height:100%}body{margin:0;display:flex;justify-content:center;align-items:center;background:#fff}.tooltip{font-family:'Montserrat', sans-serif;padding:10px}

.months{display:flex;justify-content:space-around}

.months-bar{display:inline-block;padding:20px;color:#fff;font-family:'Open Sans', sans-serif;cursor:pointer;position:relative;margin:0 10px;box-shadow:0px 0px 18px -3px rgba(0,0,0,0.3);border-radius:3px}.months-bar:hover .months-users{display:block}

